@import "../base/main.sass";

.row-controls {
  display: inline-flex;
  flex-direction: column;
  position: relative;

  &__label {
    background-color: white;
    border: 1px solid $slate-6;
    border-radius: 4px;
    display: flex;
    height: 32px;
    padding: 0;
    width: 67px;

    div {
      border-right: 1px solid $slate-6;
      display: inline-block;
      height: 100%;
      width: 47px; }

    span {
      align-items: center;
      color: $slate-1;
      display: inline-flex;
      font-size: $scale-0;
      height: 100%; }

    svg {
      color: $slate-1;
      height: 100%;
      margin: auto;
      vertical-align: middle; } }

  &__dropdown {
    position: relative;

    > div {
      background-color: white;
      border: 1px solid $slate-6;
      border-radius: 3px;
      box-shadow: 0 3px 5px $slate-6;
      display: flex;
      flex-direction: column;
      margin-top: 12px;
      position: absolute;
      width: 67px;
      z-index: 1;

      button {
        background-color: transparent;
        border: none;
        color: $slate-1;
        font-size: $scale-0;
        padding: 12px;
        text-align: left; } } } }
